import { Component, OnInit } from '@angular/core'
import { CommonModule } from '@angular/common'
import {
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonImg,
  IonButton,
  IonText,
  IonIcon,
} from '@ionic/angular/standalone'
import { addIcons } from 'ionicons'
import { logoApple, logoGoogle } from 'ionicons/icons'
import { StorageService } from 'src/app/services/utils/storage.service'
import { TranslateModule } from '@ngx-translate/core'
import { InlineSVGModule } from 'ng-inline-svg-2'
import { RouterLink } from '@angular/router'
import { HeaderComponent } from 'src/app/components/header/header.component'
import { AuthService } from 'src/app/services/external/auth.service'

@Component({
  selector: 'app-landing',
  templateUrl: './landing.page.html',
  styleUrls: ['./landing.page.scss'],
  imports: [
    IonContent,
    CommonModule,
    HeaderComponent,
    TranslateModule,
    IonRow,
    IonCol,
    IonButton,
    IonText,
    IonIcon,
    InlineSVGModule,
    RouterLink,
  ],
})
export class LandingPage implements OnInit {
  public program: any

  constructor(
    private storageService: StorageService,
    public authService: AuthService
  ) {
    addIcons({ logoApple, logoGoogle })
  }

  async ngOnInit() {
    await this.storageService.get('program').then((p) => {
      this.program = p
    })
  }

  async signInWithSocialProvider(provider: 'Apple' | 'Google') {
    this.authService.signInWithSocialProvider(provider)
  }
}
